@charset "UTF-8";
/** common scss **/
/** object scss **/
/* ===== 基础样式 ===== */
body {
  line-height: 1.5;
  font-family: "Roboto", "Helvetica Neue", "Helvetica", "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "\9ED1\4F53", Arial, sans-serif;
  font-size: 14px;
  padding: 0;
  margin: 0;
  color: #202121;
  background-color: #fff;
  text-align: center; }

a {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

button,
input,
select,
textarea {
  font-family: "Roboto", "Helvetica Neue", "Hiragino Sans GB", "Microsoft YaHei", "\9ED1\4F53", Arial, sans-serif; }

input,
select,
textarea {
  font-size: 14px; }

textarea,
input {
  color: #000; }

textarea {
  resize: none;
  overflow-y: auto; }

.app {
  position: absolute;
  top: 0;
  bottom: 0;
  max-width: 100vw;
  display: flex;
  flex-direction: column; }
  .app-progress {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%; }
    .app-progress > .MuiLinearProgress-colorPrimary {
      background-color: #fff; }
  .app header {
    flex: 1 1; }
  .app main {
    flex: 12 1;
    padding: 0 14px; }

.pre-javascript {
  border-radius: 4px; }

.layout-header-nav {
  padding: 8px 14px;
  display: flex;
  justify-content: center; }
  .layout-header-nav a {
    color: #fff; }

.nav-button {
  border-radius: 4px !important; }

.nav-box {
  box-sizing: border-box;
  width: calc(100vw - 28px);
  border-radius: 4px;
  padding: 12px;
  background-color: #1d1d1d; }
  .nav-box-text {
    font-size: 18px;
    margin: 0;
    font-weight: bold;
    line-height: 1.8; }
  .nav-box-subText {
    font-size: 12px;
    color: #bdc1c0; }

.nav-box.white .nav-box-text {
  color: #fff; }

.nav-box.red .nav-box-text {
  color: #ff5605; }

.nav-box.green .nav-box-text {
  color: #00da6d; }

.nav-box.blue .nav-box-text {
  color: #075cff; }

/** pages scss **/
.api-box {
  display: flex;
  height: 100%;
  padding: 6px 0;
  box-sizing: border-box; }

.api-menu {
  min-width: 200px;
  width: 30%;
  max-width: 240px;
  height: 100%;
  overflow-y: auto;
  border-right: 1px solid #ccc; }
  .api-menu-item {
    padding: 12px 0; }

.api-view {
  width: 100%; }
  .api-view-box {
    padding: 24px 0; }

